import React from 'react'
import { Switch, Route } from 'react-router-dom'



import Home from './components/pages2/Home'
import Search from './components/pages2/Search'
import Result from './components/pages2/Result'

import SpotifyAuth from './components/pages2/SpotifyAuth'



const Urls = () => (
 
	<div>


		<Route exact path={'https://gpitot.github.io' + process.env.PUBLIC_URL + '/'} component={Home}/>
		<Route exact path={'https://gpitot.github.io' + process.env.PUBLIC_URL + '/search'} component={Search}/>
		<Route exact path={'https://gpitot.github.io' + process.env.PUBLIC_URL + '/result'} component={Result}/>


		<Route exact path={'https://gpitot.github.io' + process.env.PUBLIC_URL + '/playlists/callback'} component={SpotifyAuth}/>
	</div>
  
)

export default Urls


